@import '../styles//view-port.scss';

html {
  font-size: 62.5%; //1 rem = 10px;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto,
    'Helvetica Neue', Arial, sans-serif;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 2rem auto;
  width: 200px;
}

.counter-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 1rem;
}
